import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import Information from './panels/Information';
import Home from './panels/Home';
import Persik from './panels/Persik';
import News from './panels/News';
import Useful from './panels/Useful';
import Contacts from './panels/Contacts';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { TabbarItem, Epic, Tabbar } from '@vkontakte/vkui';
import Icon24NewsFeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Education from '@vkontakte/icons/dist/24/education';



const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(null);
	const [history, setHistory] = useState(['home']) 
	const [scheme, SetScheme] = useState(null)

	useEffect(() => {
		window.addEventListener('popstate', () => goBack()); //  Добавляем обработчик события изменения истории для работы аппаратных кнопок.
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				SetScheme(data.scheme)
			}
		});
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	function goToPage(name) {
		window.history.pushState({panel: name}, name)
		setActivePanel(name);
		history.push(name)
	};

	function goBack() {
  		if(history.length === 1){
    		bridge.send("VKWebAppClose");
  		} else if(history.length > 1){
			history.pop()
			setActivePanel(history[history.length - 1])
  		}
	}

	return (
		<ConfigProvider isWebView={true}>
			<View activePanel={activePanel} popout={popout} onSwipeBack={goBack} history={history}>
			<Home id='home' go={go} />
			<Persik id='persik' go={go} />
			<Information id='information' go={go} />
			<Contacts id='contacts' go={go} />
			<Useful id='useful' go={go} />
			<News id='news' go={go} />
			</View>
			<Tabbar>
			<TabbarItem text="Главная" onClick={go} data-to="home"><Icon24Home/></TabbarItem>
			<TabbarItem text="Полезное" onClick={go} data-to="useful"><Icon24Education/></TabbarItem>
			<TabbarItem text="Новости" onClick={go} data-to="news"><Icon24NewsFeed/></TabbarItem>
		</Tabbar>
		</ConfigProvider>

		  
	);
}

export default App;